import {inject, onMounted} from "vue";
import {VueCookies} from "vue-cookies";
import {envVariable,useAuthStore} from "./useStates";
import {useRouter} from "vue-router";
import axios from "axios";


export const useAuth=()=>{
    const {apiBase,cookieName,apiKey}=envVariable()
    const router=useRouter()
    const {authStore}=useAuthStore()
    const $cookies = inject<VueCookies>('$cookies');
    onMounted(async ()=>{
        const token=$cookies?.get(cookieName as string)
        if(token){
            try {
                // @ts-ignore
                const userInformation:string=await axios.get(apiBase+'/api/auth/me',{
                    headers:{
                        Authorization:apiKey,
                        token:token.value
                    },
                    credentials: "include",
                    baseURL:apiBase
                })
                authStore.$patch({
                    username:userInformation,
                    isLogin:true,
                    token:token
                })
                router.push({name:'DASHBOARD'})
            }catch (err) {
                authStore.$reset()
                router.push({name:'LOGIN'})
            }
        }else{
            authStore.$reset()
            router.push({name:'LOGIN'})
        }




    })

}